import axios from "axios";

import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import AccountCircleOutlinedIcon from "@mui/icons-material/AccountCircleOutlined";
import "react-toastify/dist/ReactToastify.css";

const Profile = () => {
  const navigate = useNavigate();
  const [userdata, setuserdata] = useState({});

  const getuserData = async () => {
    try {
      const response = await axios.get("/currentuser");
      console.log(response);
      if (response.data.success) {
        console.log(response.data.data);
        setuserdata(response.data.data);
      }
    } catch (error) {
      console.log(error);
      if (error.response.status === 401) {
        navigate("/login");
        return toast.error("Session timed out! Login again");
      } else if (
        error &&
        error.response &&
        error.response.data &&
        error.response.data.error
      ) {
        return toast.error(error.response.data.error);
      }
    }
  };

  const handleLogout = async () => {
    try {
      const response = await axios.get("/logout");
      if (response.data.success) {
        navigate("/login");
        return toast.success("You were logged out successfully!");
      }
    } catch (error) {
      console.log(error);
      if (
        error &&
        error.response &&
        error.response.data &&
        error.response.data.error
      ) {
        return toast.error(error.response.data.error);
      }
    }
  };

  useEffect(() => {
    getuserData();
  }, []);
  if (userdata)
    return (
      <>
        {" "}
        <ToastContainer />
        <div className="profilepage">
          <div className="profile-mainbody">
            <AccountCircleOutlinedIcon padding={20} margin={50} />
            <h1>Hello {userdata.username}, Welcome Back!</h1>
            <div className="profile-sec-div">
              <p>
                Age :
                {new Date().getFullYear() -
                  new Date(userdata.dob).getFullYear()}{" "}
                years
              </p>
              <p>email : {userdata.email}</p>
              <p>Under 18? : {userdata.isUnder18 ? "Yes" : "No"}</p>
              <p>Phone number : {userdata.mobileno} </p>
              <button
                className="profile-button"
                type="button"
                onClick={() => handleLogout()}
              >
                Log Out
              </button>
            </div>
          </div>
        </div>
      </>
    );
};

export default Profile;
